import { FormEventHandler, MouseEventHandler, useState } from 'react';
import { Helmet } from 'react-helmet-async';
import { t } from '../../../helpers/i18n/dictionary';
import useAuth from '../../../hooks/auth/useAuth';
import ActionButton from '../../Buttons/ActionButton';

const YouAuthLogin = () => {
  const [password, setPassword] = useState('a');
  const [state, setState] = useState<'loading' | 'error' | 'success' | undefined>();
  const { authenticate, forceSetPassword_temp } = useAuth();

  const forcePassword: MouseEventHandler = async (e) => {
    e.preventDefault();

    const success = await forceSetPassword_temp('a');
    if (success) {
      alert('Password was successfully set to a\n\nPress Sign in now');
      setPassword('a');
    }

    return false;
  };

  const doLogin: FormEventHandler = async (e) => {
    e.preventDefault();
    setState('loading');
    try {
      const result = await authenticate(password);

      if (result) {
        setState('success');
      }
    } catch (ex) {
      setState('error');
      // Todo show message what failed
      console.error(ex);
    }
  };

  return (
    <>
      <Helmet>
        <title>Login | Owner console</title>
      </Helmet>
      <section className="body-font flex h-full pt-24 text-gray-600">
        <div className="container m-auto h-full max-w-[30rem] p-5">
          <form onSubmit={doLogin}>
            <h1 className="text-xl">YouAuth</h1>
            <label
              htmlFor="youniverse-id"
              className="text-sm leading-7 text-gray-600 dark:text-gray-400"
            >
              Youniverse id
            </label>
            <input
              type="text"
              name="youniverse-id"
              id="youniverse-id"
              required
              className="w-full rounded border border-gray-300 bg-gray-100 bg-opacity-50 py-1 px-3 text-base leading-8 text-gray-700 outline-none transition-colors duration-200 ease-in-out focus:border-indigo-500 focus:bg-transparent focus:ring-2 focus:ring-indigo-200 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-100"
              defaultValue={window.location.hostname}
              disabled={true}
              readOnly={true}
            />
            <label
              htmlFor="password"
              className="text-sm leading-7 text-gray-600 dark:text-gray-400"
            >
              Password
            </label>
            <input
              type="text"
              name="password"
              id="password"
              required
              className="w-full rounded border border-gray-300 bg-gray-100 bg-opacity-50 py-1 px-3 text-base leading-8 text-gray-700 outline-none transition-colors duration-200 ease-in-out focus:border-indigo-500 focus:bg-transparent focus:ring-2 focus:ring-indigo-200 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-100"
              onChange={(e) => setPassword(e.target.value)}
            />
            <ActionButton className="mt-5 w-full" type="primary" icon="send" state={state}>
              {t('login')}
            </ActionButton>

            <ActionButton className="mt-2 block w-full" type="secondary" onClick={forcePassword}>
              {t("Force password to 'a'")}
            </ActionButton>
          </form>
        </div>
      </section>
    </>
  );
};

export default YouAuthLogin;
