import { FC, ReactNode, useRef, useState } from 'react';
import useOutsideTrigger from '../../hooks/clickedOutsideTrigger/useClickedOutsideTrigger';
import ConfirmDialog from '../Dialog/ConfirmDialog/ConfirmDialog';
import Arrow from '../Icons/Arrow/Arrow';
import Check from '../Icons/Check/Check';
import Exclamation from '../Icons/Exclamation/Exclamation';
import Loader from '../Icons/Loader/Loader';
import Pencil from '../Icons/Pencil/Pencil';
import Plus from '../Icons/Plus/Plus';
import Save from '../Icons/Save/Save';
import Trash from '../Icons/Trash/Trash';

export type ActionButtonState = 'loading' | 'success' | 'error' | 'idle';

type ActionButtonProps = {
  children?: ReactNode;
  className?: string;
  icon?: 'save' | 'send' | 'plus' | 'trash' | 'edit' | 'up' | 'down';
  type?: 'primary' | 'secondary' | 'remove' | 'mute';
  state?: ActionButtonState;
  onClick?: (e) => void;
  title?: string;
  size?: 'large' | 'small';
  confirmOptions?: {
    title: string;
    buttonText: string;
    body: string;
  };
  options?: { value: string; name: string }[];
};

export const mergeStates = (
  stateA: ActionButtonState,
  stateB: ActionButtonState
): ActionButtonState => {
  if (stateA === 'error' || stateB === 'error') {
    return 'error';
  }

  if (stateA === 'loading' || stateB === 'loading') {
    return 'loading';
  }

  if (stateA === 'idle' && stateB === 'idle') {
    return 'idle';
  }

  if (stateA === 'success' && stateB === 'success') {
    return 'success';
  }

  if ((stateA === 'success' && stateB === 'idle') || (stateA === 'idle' && stateB === 'success')) {
    return 'success';
  }
};

const ActionButton: FC<ActionButtonProps> = ({
  children,
  onClick,
  className,
  icon,
  type,
  state,
  title,
  size,
  confirmOptions,
  options,
}) => {
  const Icon = (props: { className: string }) => {
    if (state === 'loading') {
      return <Loader {...props} />;
    }
    if (state === 'success') {
      return <Check {...props} />;
    }
    if (state === 'error') {
      return <Exclamation {...props} />;
    }

    return icon === 'save' ? (
      <Save {...props} />
    ) : icon === 'send' ? (
      <Arrow {...props} />
    ) : icon === 'plus' ? (
      <Plus {...props} />
    ) : icon === 'trash' ? (
      <Trash {...props} />
    ) : icon === 'edit' ? (
      <Pencil {...props} />
    ) : icon === 'up' ? (
      <Arrow {...props} className={`-rotate-90 ${props.className}`} />
    ) : icon === 'down' || options?.length ? (
      <Arrow {...props} className={`rotate-90 ${props.className}`} />
    ) : (
      <></>
    );
  };

  const wrapperRef = useRef(null);
  useOutsideTrigger(wrapperRef, () => setIsOpen(false));

  const [needsConfirmation, setNeedsConfirmation] = useState(false);
  const [isOpen, setIsOpen] = useState(false);

  const colorClasses =
    state === 'error'
      ? 'bg-red-500 hover:bg-red-600 text-white'
      : type === 'secondary'
      ? 'bg-slate-100 hover:bg-slate-200 dark:bg-slate-700 dark:hover:bg-slate-800 dark:text-white'
      : type === 'remove'
      ? 'bg-red-200 hover:bg-red-400 dark:bg-red-700 hover:dark:bg-red-800 dark:text-white'
      : type === 'mute'
      ? ''
      : 'bg-green-500 hover:bg-green-600 text-white';

  const widthClasses =
    children && type !== 'mute'
      ? `min-w-[6rem] ${className?.indexOf('w-full') ? '' : 'w-full sm:w-auto'}`
      : '';

  const sizeClasses =
    size === 'large' ? 'px-5 py-3' : size === 'small' ? 'px-3 py-1 text-sm' : 'px-3 py-2';

  const stateClasses = state === 'loading' ? 'animate-pulse' : '';

  return (
    <>
      <button
        className={`relative flex flex-row rounded-md text-left ${widthClasses} ${sizeClasses} ${colorClasses} ${stateClasses} ${className}`}
        disabled={state === 'loading'}
        onClick={
          options
            ? (e) => setIsOpen(true)
            : confirmOptions
            ? (e) => {
                e.preventDefault();
                setNeedsConfirmation(true);
                return false;
              }
            : onClick
        }
        title={title}
        ref={wrapperRef}
      >
        {children && <span className="mr-1">{children}</span>}
        <Icon className={'my-auto ml-auto h-4 w-4'} />
        {options ? (
          <ul
            className={`absolute right-0 bottom-[100%] ${
              isOpen ? 'max-h-[15rem]' : 'max-h-0'
            } overflow-auto border border-gray-100 bg-white text-black`}
          >
            {options.map((option) => (
              <li
                onClick={() => onClick(option.value)}
                className="px-2 py-1 hover:bg-slate-200"
                key={option.value}
              >
                {option.name}
              </li>
            ))}
          </ul>
        ) : null}
      </button>
      {confirmOptions && (
        <ConfirmDialog
          title={confirmOptions.title}
          confirmText={confirmOptions.buttonText}
          needConfirmation={needsConfirmation}
          onConfirm={onClick}
          onCancel={() => {
            setNeedsConfirmation(false);
          }}
        >
          <p className="text-sm">{confirmOptions.body}</p>
        </ConfirmDialog>
      )}
    </>
  );
};

export default ActionButton;
